.hero {
    padding: 100px 0;
    background-color: #e5f1ff;
    text-align: center;
  }
  
  .hero .container {
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .hero h1 {
    font-size: 2.5rem;
    margin-bottom: 20px;
  }
  
  .hero .btn {
    display: inline-block;
    padding: 10px 20px;
    background-color: #0077cc;
    color: white;
    border-radius: 5px;
    text-decoration: none;
    font-weight: bold;
    transition: background-color 0.3s ease;
  }
  
  .hero .btn:hover {
    background-color: #005fa3;
  }
  