.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .modal-content {
    background: white;
    padding: 30px;
    border-radius: 10px;
    max-width: 800px;
    width: 90%;
    position: relative;
    overflow-y: auto;
    max-height: 90vh;
  }
  
  .close-btn {
    position: absolute;
    right: 20px;
    top: 20px;
    font-size: 24px;
    cursor: pointer;
  }
  
  .modal-body img.modal-project-image {
    max-width: 100%;
    margin: 10px 0;
    border-radius: 8px;
  }
  
  .ul-margin {
    margin-bottom: 10px;
  }
  