.projects {
    padding: 60px 0;
    background-color: #f5f5f5;
  }
  
  .projects .project-list {
    display: flex;
    flex-direction: column;
    gap: 30px;
  }
  
  .projects .project-item {
    background: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    cursor: pointer;
  }
  
  .projects .project-item h3 {
    margin-bottom: 10px;
    font-size: 1.5rem;
  }
  
  .projects .project-item .flex {
    display: flex;
    gap: 10px;
    margin-bottom: 10px;
  }
  
  .projects .project-item img {
    width: 100px;
    height: auto;
    border-radius: 8px;
  }
  