.site-header {
    background-color: #000;
    padding: 20px 0;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 1000;
    border-bottom: 1px solid #333;
  }
  
  .site-header .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .logo {
    font-size: 1.8rem;
    font-weight: bold;
    color: #fff;
  }
  
  .main-nav ul {
    list-style: none;
    display: flex;
  }
  
  .main-nav ul li {
    margin-left: 20px;
  }
  
  .main-nav ul li a {
    color: #eee;
    text-decoration: none;
    transition: color 0.3s;
  }
  
  .main-nav ul li a:hover {
    color: #ff5722;
  }
  